import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { StatusCommandItemModel } from '../models/status-command-item.model';

@Injectable({
  providedIn: 'root',
})
export class StatusCommandService {
  private items$ = new BehaviorSubject<StatusCommandItemModel[]>([
    /*{
      id: 1,
      name: 'name command 1',
      price: 10,
    },*/
  ]);

  getCommand() {
    return this.items$.asObservable();
  }

  addToCommand(newItem: StatusCommandItemModel) {
    this.items$.next([...this.items$.getValue(), newItem]);
  }
  clearCart() {
    this.items$.next([]);
  }

  removeItem(id: number) {
    this.items$.next(this.items$.getValue().filter((item) => item.id !== id));
  }

}
