import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomePage} from './home.page';
import {CanalService} from '../../services/canal.service';


// @ts-ignore
const routes: Routes = [
  {
    path: '',
    component: HomePage,
    children: [
      {
        path: 'menu',
        loadChildren: () =>
          import('../screens/listing/listing.module').then(
            (m) => m.ListingPageModule
          ),
        data: { canal: 'Online', user: '5' } // Ajoutez les paramètres de requête ici

      },
      {
        path: 'cart',
        loadChildren: () =>
          import('../screens/cart/cart.module').then((m) => m.CartPageModule),
      },
      {
        path: 'detail/:id',
        loadChildren: () =>
          import('../screens/detail/detail.module').then((m) => m.DetailPageModule),
      },
      {
        path: 'statuscommand',
        loadChildren: () =>
          import('../screens/status-command/status-command.module').then((m) => m.StatusCommandModule),
      },
      {
        path: 'list-sub-food/:id',
        loadChildren: () =>
          import('../screens/list-sub-food/list-sub-food.module').then((m) => m.ListSubFoodModule),
      },
      {
        path: '**',
        redirectTo: `/client/menu`,
        pathMatch: 'full'
      }
    ],
  },
  {
    path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomePageRoutingModule {
}
