import {Component, OnInit} from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';
import {CartService} from '../../services/cart.service';
import {CartItem} from '../../models/cart-item.model';
import {StatusCommandItemModel} from '../../models/status-command-item.model';
import {StatusCommandService} from '../../services/status-command.service';
import {CanalService} from '../../services/canal.service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {
  canal: string;
  userId: number;
  private cartItemCount = new BehaviorSubject(0);
  // eslint-disable-next-line @typescript-eslint/member-ordering
  cartItems$: Observable<CartItem[]>;
  // eslint-disable-next-line @typescript-eslint/member-ordering
  commandItems$: Observable<StatusCommandItemModel[]>;
  constructor(
    private cartService: CartService,
    private statusCommandService: StatusCommandService,
    private canalService: CanalService,
    private router: Router,
    private route: ActivatedRoute
  ) {
  }


  ngOnInit() {
    this.cartItems$ = this.cartService.getCart();
    this.commandItems$ = this.statusCommandService.getCommand();
  }

  goToMenu(): void {
    this.userId= this.canalService.getUserId();
    this.canal= this.canalService.getCanal();
    const queryParams = { canal: this.canal, user: this.userId };
    this.router.navigate(['/client/menu'], { queryParams });
  }
}
